import styles from "./styles/GallerySection.module.css";
import { useState, useEffect, useRef } from "react";
import { createPortal } from "react-dom";
import gsap from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";
import { IoIosArrowBack, IoIosArrowForward, IoMdClose } from "react-icons/io";

function GallerySection() {
  const [currentImageIndex, setCurrentImageIndex] = useState(0);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const titleRef = useRef(null);
  const carouselRef = useRef(null);
  
  //   
  const [touchStart, setTouchStart] = useState(null);
  const [touchEnd, setTouchEnd] = useState(null);
  const [isTouchMove, setIsTouchMove] = useState(false);
  const [isMultiTouch, setIsMultiTouch] = useState(false);
  const [isPinchZoom, setIsPinchZoom] = useState(false);
  
  //   
  const [isSliding, setIsSliding] = useState(false);
  const modalImageRef = useRef(null);

  //    
  const [showCounter, setShowCounter] = useState(true);
  const counterTimerRef = useRef(null);

  //    (  )
  const TOTAL_IMAGES = 15;
  
  //     ()
  const COUNTER_HIDE_DELAY = 1500;
  
  //    
  const images = Array.from({ length: TOTAL_IMAGES }, (_, index) => {
    const imageNumber = (index + 1).toString().padStart(3, '0');
    return {
      src: `/new_gallery/${imageNumber}.jpg`,
      alt: `  ${index + 1}`,
    };
  });

  //   
  const isMobileDevice = () => {
    return /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent) ||
           window.innerWidth <= 768;
  };

  //    
  const minSwipeDistance = 50;

  //     
  const showCounterWithTimer = () => {
    setShowCounter(true);
    
    //    
    if (counterTimerRef.current) {
      clearTimeout(counterTimerRef.current);
    }
    
    //   
    counterTimerRef.current = setTimeout(() => {
      setShowCounter(false);
    }, COUNTER_HIDE_DELAY);
  };

  //      
  const slideToImage = (newIndex, direction) => {
    if (isSliding) return;
    
    setIsSliding(true);
    
    //     
    showCounterWithTimer();
    
    //  
    if (modalImageRef.current) {
      const slideInDistance = direction === 'left' ? '100%' : '-100%';
      
      //    will-change  
      modalImageRef.current.style.willChange = 'transform, opacity';
      
      //    ()
      gsap.set(modalImageRef.current, {
        opacity: 0,
        scale: 0.9
      });
      
      //      
      setTimeout(() => {
        setCurrentImageIndex(newIndex);
        
        //       
        requestAnimationFrame(() => {
          requestAnimationFrame(() => {
            if (modalImageRef.current) {
              //    
              gsap.set(modalImageRef.current, {
                x: slideInDistance,
                opacity: 0,
                scale: 0.9
              });
              
              //    
              gsap.to(modalImageRef.current, {
                x: 0,
                opacity: 1,
                scale: 1,
                duration: 0.3,
                ease: "power2.out",
                onComplete: () => {
                  setIsSliding(false);
                  //    will-change  
                  if (modalImageRef.current) {
                    modalImageRef.current.style.willChange = 'auto';
                  }
                }
              });
            }
          });
        });
      }, 50); // 50ms     
    } else {
      //        
      setCurrentImageIndex(newIndex);
      setIsSliding(false);
    }
  };

  const handlePrevImage = () => {
    if (isSliding) return;
    const newIndex = currentImageIndex > 0 ? currentImageIndex - 1 : images.length - 1;
    
    if (isModalOpen) {
      slideToImage(newIndex, 'right');
    } else {
      setCurrentImageIndex(newIndex);
    }
  };

  const handleNextImage = () => {
    if (isSliding) return;
    const newIndex = currentImageIndex < images.length - 1 ? currentImageIndex + 1 : 0;
    
    if (isModalOpen) {
      slideToImage(newIndex, 'left');
    } else {
      setCurrentImageIndex(newIndex);
    }
  };

  const handleImageClick = () => {
    setIsModalOpen(true);
  };

  const handleCloseModal = () => {
    setIsModalOpen(false);
    setIsSliding(false);
    setIsTouchMove(false);
    setIsMultiTouch(false);
    setIsPinchZoom(false);
    setShowCounter(true);
    
    //  
    if (counterTimerRef.current) {
      clearTimeout(counterTimerRef.current);
      counterTimerRef.current = null;
    }
  };

  //     (  )
  const handleModalBackgroundClick = (e) => {
    //        
    if (isTouchMove || isPinchZoom) {
      setIsTouchMove(false);
      setIsPinchZoom(false);
      return;
    }
    
    //      
    if (e.target === e.currentTarget) {
      handleCloseModal();
    }
  };

  //   
  const onTouchStart = (e) => {
    setIsTouchMove(false);
    
    //   ( )
    if (e.touches && e.touches.length > 1) {
      setIsMultiTouch(true);
      setIsPinchZoom(true);
      return;
    }
    
    setIsMultiTouch(false);
    
    if (!isModalOpen || !isMobileDevice() || isSliding || isPinchZoom) return;
    setTouchEnd(null); //    
    setTouchStart(e.targetTouches[0].clientX);
  };

  //   
  const onTouchMove = (e) => {
    setIsTouchMove(true);
    
    //     
    if (e.touches && e.touches.length > 1) {
      setIsMultiTouch(true);
      setIsPinchZoom(true);
      return;
    }
    
    //      
    if (!isModalOpen || !isMobileDevice() || isSliding || isPinchZoom || isMultiTouch) return;
    setTouchEnd(e.targetTouches[0].clientX);
  };

  //   
  const onTouchEnd = (e) => {
    //     (  )
    if (e.touches && e.touches.length > 0) {
      return; //   
    }
    
    //     
    if (isMultiTouch || isPinchZoom) {
      setIsMultiTouch(false);
      //       (  )
      setTimeout(() => {
        setIsPinchZoom(false);
      }, 100);
      return;
    }
    
    if (!isModalOpen || !isMobileDevice() || isSliding || isPinchZoom) return;
    if (!touchStart || !touchEnd) return;
    
    const distance = touchStart - touchEnd;
    const isLeftSwipe = distance > minSwipeDistance;
    const isRightSwipe = distance < -minSwipeDistance;

    if (isLeftSwipe) {
      handleNextImage();
    } else if (isRightSwipe) {
      handlePrevImage();
    }
  };

  const handleKeyDown = (e) => {
    if (isSliding) return;
    
    if (e.key === 'ArrowLeft') {
      e.preventDefault();
      handlePrevImage();
    } else if (e.key === 'ArrowRight') {
      e.preventDefault();
      handleNextImage();
    } else if (e.key === 'Escape' && isModalOpen) {
      e.preventDefault();
      handleCloseModal();
    }
    //    
    if (e.key === 'F12' || 
        (e.ctrlKey && e.shiftKey && e.key === 'I') ||
        (e.ctrlKey && e.shiftKey && e.key === 'C') ||
        (e.ctrlKey && e.shiftKey && e.key === 'J') ||
        (e.ctrlKey && e.key === 'U') ||
        (e.ctrlKey && e.key === 'S')) {
      e.preventDefault();
      return false;
    }
  };

  //  
  const handleContextMenu = (e) => {
    e.preventDefault();
    return false;
  };

  //  
  const handleDragStart = (e) => {
    e.preventDefault();
    return false;
  };

  //      
  const handleImageLoad = (e) => {
    e.target.setAttribute('draggable', 'false');
    e.target.style.userSelect = 'none';
    e.target.style.WebkitUserSelect = 'none';
    e.target.style.MozUserSelect = 'none';
    e.target.style.msUserSelect = 'none';
    e.target.style.pointerEvents = 'none';
  };

  //    (Safari  )
  useEffect(() => {
    // Safari 
    const isSafari = /^((?!chrome|android).)*safari/i.test(navigator.userAgent);
    
    if (isSafari) {
      // Safari     ( )
      return;
    }

    let devtools = {
      open: false,
      orientation: null
    };

    const threshold = 200; //    

    const checkDevTools = () => {
      if (window.outerHeight - window.innerHeight > threshold || 
          window.outerWidth - window.innerWidth > threshold) {
        if (!devtools.open) {
          devtools.open = true;
          console.clear();
          document.body.innerHTML = '<div style="display: flex; justify-content: center; align-items: center; height: 100vh; font-family: Arial; font-size: 24px; color: #333;">  .</div>';
        }
      } else {
        devtools.open = false;
      }
    };

    const interval = setInterval(checkDevTools, 1000); //   

    //   
    const originalLog = console.log;
    const originalError = console.error;
    const originalWarn = console.warn;
    
    console.log = () => {};
    console.error = () => {};
    console.warn = () => {};

    return () => {
      clearInterval(interval);
      console.log = originalLog;
      console.error = originalError;
      console.warn = originalWarn;
    };
  }, []);

  //      
  useEffect(() => {
    const handleGlobalKeyDown = (e) => {
      if (e.key === 'F12' || 
          (e.ctrlKey && e.shiftKey && e.key === 'I') ||
          (e.ctrlKey && e.shiftKey && e.key === 'C') ||
          (e.ctrlKey && e.shiftKey && e.key === 'J') ||
          (e.ctrlKey && e.key === 'U') ||
          (e.ctrlKey && e.key === 'S')) {
        e.preventDefault();
        return false;
      }
    };

    const handleGlobalContextMenu = (e) => {
      e.preventDefault();
      return false;
    };

    const handleGlobalSelectStart = (e) => {
      //      
      if (e.target.tagName === 'IMG' || e.target.closest('.container')) {
        e.preventDefault();
        return false;
      }
    };

    const handleGlobalDragStart = (e) => {
      if (e.target.tagName === 'IMG') {
        e.preventDefault();
        return false;
      }
    };

    document.addEventListener('keydown', handleGlobalKeyDown);
    document.addEventListener('contextmenu', handleGlobalContextMenu);
    document.addEventListener('selectstart', handleGlobalSelectStart);
    document.addEventListener('dragstart', handleGlobalDragStart);

    return () => {
      document.removeEventListener('keydown', handleGlobalKeyDown);
      document.removeEventListener('contextmenu', handleGlobalContextMenu);
      document.removeEventListener('selectstart', handleGlobalSelectStart);
      document.removeEventListener('dragstart', handleGlobalDragStart);
    };
  }, []);

  // GSAP  
  useEffect(() => {
    gsap.registerPlugin(ScrollTrigger);

    // ScrollTrigger  ( )
    ScrollTrigger.config({
      ignoreMobileResize: true,
      autoRefreshEvents: "visibilitychange,DOMContentLoaded,load",
    });

    //  :   
    gsap.set([titleRef.current, carouselRef.current], {
      opacity: 0,
      y: 30,
    });

    const tl = gsap.timeline({
      scrollTrigger: {
        trigger: titleRef.current,
        start: "top 70%",
        end: "center center",
        toggleActions: "play none none reverse",
        invalidateOnRefresh: false,
      },
    });

    //  
    tl.to(titleRef.current, {
      opacity: 1,
      y: 0,
      duration: 0.8,
      ease: "power2.out",
    }).to(carouselRef.current, {
      opacity: 1,
      y: 0,
      duration: 1,
      ease: "power2.out",
    }, "-=0.3");

    return () => {
      tl.kill();
    };
  }, []);

  //     (   )
  useEffect(() => {
    const setVH = () => {
      const vh = window.innerHeight * 0.01;
      document.documentElement.style.setProperty('--vh', `${vh}px`);
    };

    setVH();
    window.addEventListener('resize', setVH);
    window.addEventListener('orientationchange', setVH);

    return () => {
      window.removeEventListener('resize', setVH);
      window.removeEventListener('orientationchange', setVH);
    };
  }, []);

  //     
  useEffect(() => {
    if (isModalOpen) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = 'unset';
    }

    return () => {
      document.body.style.overflow = 'unset';
    };
  }, [isModalOpen]);

  //        
  useEffect(() => {
    if (isModalOpen) {
      showCounterWithTimer();
    }
    
    //     
    return () => {
      if (counterTimerRef.current) {
        clearTimeout(counterTimerRef.current);
      }
    };
  }, [isModalOpen]);

  return (
    <div 
      className={styles.container} 
      onKeyDown={handleKeyDown} 
      onContextMenu={handleContextMenu}
      onDragStart={handleDragStart}
      tabIndex={0}
    >
      <div className={styles.content}>
        <h2 ref={titleRef} className={styles.title}>Gallery</h2>
        
        <div ref={carouselRef} className={styles.carouselContainer}>
          <button 
            className={styles.navButton} 
            onClick={handlePrevImage}
            aria-label=" "
            disabled={isSliding}
          >
            <IoIosArrowBack />
          </button>
          
          <div className={styles.polaroidFrame} onClick={handleImageClick}>
            <div className={styles.polaroidInner}>
              <img
                src={images[currentImageIndex].src}
                alt={images[currentImageIndex].alt}
                className={styles.carouselImage}
                loading="lazy"
                decoding="async"
                draggable="false"
                onLoad={handleImageLoad}
                onContextMenu={handleContextMenu}
                onDragStart={handleDragStart}
                style={{
                  userSelect: 'none',
                  WebkitUserSelect: 'none',
                  MozUserSelect: 'none',
                  msUserSelect: 'none',
                  pointerEvents: 'none'
                }}
              />
              <div className={styles.polaroidCaption}>
                {currentImageIndex + 1} / {images.length}
              </div>
            </div>
          </div>
          
          <button 
            className={styles.navButton} 
            onClick={handleNextImage}
            aria-label=" "
            disabled={isSliding}
          >
            <IoIosArrowForward />
          </button>
        </div>
      </div>

      {/*    */}
      {isModalOpen && createPortal(
        <div 
          className={styles.modal} 
          onClick={handleModalBackgroundClick}
          onContextMenu={handleContextMenu}
          onDragStart={handleDragStart}
          onTouchStart={onTouchStart}
          onTouchMove={onTouchMove}
          onTouchEnd={onTouchEnd}
        >
          <div 
            className={styles.modalContent}
          >
            <button 
              className={styles.closeButton} 
              onClick={handleCloseModal}
              aria-label=""
            >
              <IoMdClose />
            </button>
            
            <button 
              className={styles.modalNavButton} 
              onClick={handlePrevImage}
              style={{ left: '20px' }}
              aria-label=" "
              disabled={isSliding}
            >
              <IoIosArrowBack />
            </button>
            
            <img
              ref={modalImageRef}
              src={images[currentImageIndex].src}
              alt={images[currentImageIndex].alt}
              className={`${styles.modalImage} ${isSliding ? styles.sliding : ''}`}
              draggable="false"
              onLoad={handleImageLoad}
              onContextMenu={handleContextMenu}
              onDragStart={handleDragStart}
              onTouchStart={onTouchStart}
              onTouchMove={onTouchMove}
              onTouchEnd={onTouchEnd}
              style={{
                userSelect: 'none',
                WebkitUserSelect: 'none',
                MozUserSelect: 'none',
                msUserSelect: 'none',
                pointerEvents: 'auto',
                touchAction: 'pinch-zoom'
              }}
            />
            
            <button 
              className={styles.modalNavButton} 
              onClick={handleNextImage}
              style={{ right: '20px' }}
              aria-label=" "
              disabled={isSliding}
            >
              <IoIosArrowForward />
            </button>

            <div className={`${styles.modalImageCounter} ${showCounter ? styles.counterVisible : styles.counterHidden}`}>
              {currentImageIndex + 1} / {images.length}
            </div>
          </div>
        </div>,
        document.body
      )}
    </div>
  );
}

export default GallerySection;
